

.MainContainer {
    height: 100%;
    width: 100%;
    min-height: 620px;
    min-width: 480px;
    background-color: #33454e;
    display: flex;
    align-items: center;
    justify-content: center;
}

.LoginContainer {
    width: 30%;
    height: 40%;
    min-width: 420px;
    min-height: 520px;
    background-color: rgba(255, 255, 255, 0.459);
    border-radius: 16px;
    box-shadow: 0px 0px 12px;
}

.SwitchContainer {
    margin-top: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.Switch {
    width: 92px;
    height: 32px;
    background-color: white;
    border: solid 1px  black;
    border-radius: 32px;
    display: flex;
    align-items: center;
    cursor: pointer;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.384);
}

.SwitchCircle {
    width: 48px;
    height: 28px;
    border-radius: 28px;
    transition: all 1s;
}

.LoginSwitch {
    background-color: #d9abff;
    transform: translateX(2px);
}

.Active {
    color: black;
    transition: all 1s;
}

.Passive {
    color: rgb(116, 116, 116);
    transition: all 1s;
}

.SignUpSwitch {
    background-color: #ffabab;
    transform: translateX(42px);
}

.InputContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}

.Input {
    width: 320px;
    height: 32px;
    margin-top: 16px;
    border-radius: 32px;
    border: solid 1px rgba(255, 255, 255, 0.425);
    background-color: #1b1b1b2a;
    text-align: center;
    box-shadow: 0px 0px 12px rgba(199, 199, 199, 0.507);
    transition: all 1s;
}

.Input::placeholder {
    color: rgb(66, 66, 66);
}

.ErrorMessage {
    height: 12px;
    font-size: 12px;
    margin: 2px;
    color: rgb(190, 0, 0);
}

.ButtonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.LSButton {
    width: 320px;
    height: 32px;
    margin-top: 60px;
    border-radius: 32px;
    border: solid 1px rgb(129, 129, 129);
    text-align: center;
    cursor: pointer;
    transition: all 2s;
    transition: transform 1s;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.384);
}

.LSButton:hover {
    transform: perspective(400px) translateZ(20px);
}

.LoginButton {
    background-color: #d9abff;
}

.SignUpButton {
    background-color: #ffabab;
}

.PassiveInput {
    width: 320px;
    height: 0px;
    margin-top: 16px;
    border-radius: 32px;
    border: solid 1px rgba(255, 255, 255, 0.425);
    background-color: #1b1b1b2a;
    text-align: center;
    box-shadow: 0px 0px 12px rgba(199, 199, 199, 0.507);
    opacity: 0;
    cursor: default;
    transition: opacity 1s, height 1s;
}