
.Menu {
    position: absolute;
    height: 0px;
}

.FillBackground {
    background-color: black;
    position: absolute;
    z-index: 1;
    transition: opacity 1s;
}

.ActiveBackground {
    opacity: 0.8;
    width: 100vw;
    height: 100vh;
}

.PassiveBackground {
    opacity: 0;
    width: 0px;
    height: 0px;
}

.MenuContainer {
    height: 100vh;
    width: 240px;
    background-color: #33454e;
    border-right:solid 2px rgba(255, 255, 255, 0.507);
    position: absolute;
    z-index: 3;
    transition: all 1s;
}

.ActiveMenu {
    transform: translateX(0px);
}

.PassiveMenu {
    transform: translateX(-100%);
}

.MenuButtonContainer {
    height: 0px;
    width: 100vw;
    display: flex;
    justify-content: right;
}
.MenuButton {
    margin: 12px;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    background-color: #92929277;
    border: solid 1px rgba(255, 255, 255, 0.568);
    cursor: pointer;
    z-index: 3;
}

.MenuItemTitle {
    margin: 0px;
    margin-left: 12px;
    margin-top: 24px;
    margin-bottom: 4px;
    font-size: 24px;
    color: rgb(165, 165, 165);
    cursor: pointer;
}

.MenuItemLine {
    border: solid 1px rgb(165, 165, 165);
}