.FriendContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
}

.RequestContainer {
    height: 100vh;
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.RequestList {
    height: 98%;
    width: 98%;
    background-color: rgba(255, 255, 255, 0.507);
    border-radius: 12px;
    box-shadow: 0px 0px 12px black;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.FriendRequestTitle {
    margin: 12px;
    font-size: 24px;
}

.requestContainer {
    height: 84%;
    width: 100%;
}

.RequestSendContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.IDInputBox {
    height: 20px;
    width: 240px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.219);
    border: solid 1px rgba(255, 255, 255, 0.582);
    text-indent: 8px;
}

.IDInputBox::placeholder {
    color: rgb(48, 48, 48);
}

.SendButton {
    margin-left: 12px;
    border-radius: 12px;
    border: solid 1px black;
    cursor: pointer;
}

.FriendItemContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.UserNameContainer {
    width: 100%;
    margin-top: 24px;
    margin-bottom: 12px;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    cursor: pointer;
}

.UserImage {
    background-color: white;
    width: 48px;
    height: 48px;
    margin: 0px;
    margin-left: 24px;
    border-radius: 50%;
}

.UserName {
    font-size: 24px;
    margin: 0px;
    margin-left: 20px;
}

.FriendItemBorder {
    width: 92%;
    border: solid 1px rgba(255, 255, 255, 0.438);
}

.RequestDetailContainer {
    width: calc(100vw - 400px);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.RequestDetail {
    width: 98%;
    height: 98%;
    background-color: rgba(255, 255, 255, 0.507);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.displayUserName {
    font-size: 24px;
}

.buttonBox {
    display: flex;
    gap: 20px;
}

.Button {
    width: 120px;
    height: 40px;
    font-size: 16px;
    border-radius: 40px;
    border: solid 1px white;
    cursor: pointer;
    background-color: rgb(216, 255, 157);
}

.Button2 {
    width: 120px;
    height: 40px;
    font-size: 16px;
    border-radius: 40px;
    border: solid 1px white;
    cursor: pointer;
    background-color: rgb(255, 183, 183);
}