.Chat {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.ChatFriendMenuContainer {
    height: 98%;
    width: 32%;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
}

.ChatBoxContainer {
    height: 98%;
    width: 66%;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.FriendListContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.UserContainer {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    margin-top: 24px;
    margin-left: 12px;
    cursor: pointer;
}

.UserIcon {
    height: 48px;
    width: 48px;
    background-color: white;
    border-radius: 50%;
}

.FriendUserName {
    font-size: 24px;
    align-self: left;
    margin: 0px;
    margin-left: 20px;
}

.FriendItemBorder {
    width: 92%;
    border: solid 1px rgba(0, 0, 0, 0.401);
    align-self: center;
}

.ChatBox {
    width: 100%;
    height: calc(100% - 100px);
    overflow: auto;
}

.InputContainer {
    width: 100%;
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 12px;
}

.InputText {
    width: calc(100% - 120px);
    border-radius: 20px;
    border: solid 1px black;
    text-indent: 12px;
}

.SendButton {
    cursor: pointer;
}

.MessageItem {
    font-size: 16px;
    margin-bottom: 0px;
}
.MessageTime {
    font-size: 8px;
    color: rgb(75, 75, 75);
}

.MyMessage {
    display: flex;
    flex-direction: column;
    text-align: right;
    margin-left: 24px;
    margin-right: 24px;
}

.OtherMessage {
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 24px;
    margin-right: 24px;
}